



































































import {Component, Vue} from 'vue-property-decorator';
import firebase from 'firebase/app';
import 'firebase/auth';
import Footer from '@/components/Footer.vue';

@Component({
    components: {
        'v-footer': Footer,
    },
})
export default class SignUpView extends Vue {
    private email: string = '';
    private password: string = '';
    private valid = false;
    private passRules = [
        (v: any) => !!v || 'Name is required',
    ];

    private emailRules = [
        (v: any) => !!v || 'E-mail is required',
        (v: any) => /.+@.+/.test(v) || 'E-mail must be valid',
    ];

    private message = '';

    public mounted() {
        window.scrollTo(0, 0);
    }

    public create() {
        firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then((res) => {
            if (res == null) {
                // console.log('not get response');
                // alert('auth failed');
                return;
            }
            if (res.user == null) {
                // console.log('user not found');
                // alert('auth failed');
                return;
            }
            res.user.getIdToken()
                .then((idToken) => {
                    localStorage.setItem('token', idToken.toString());
                    this.$router.push('/bookshelf');
                }).catch((err) => {
                // console.log(err);
                console.log('firebase get token error');
            });
        }).catch((err) => {
            this.message = 'アカウントの作成に失敗しました。';
            // alert('ログインエラー');
            // console.log(err);
        });
    }

    public loginWithGoogle() {
        const provider = new firebase.auth.GoogleAuthProvider();
        this.loginOps(firebase.auth().signInWithPopup(provider));
    }

    private loginOps(p: Promise<firebase.auth.UserCredential>) {
        p.then((res) => {
            if (res == null) {
                // console.log('not get response');
                // alert('auth failed');
                return;
            }
            if (res.user == null) {
                // console.log('user not found');
                // alert('auth failed');
                return;
            }
            res.user.getIdToken()
                .then((idToken) => {
                    localStorage.setItem('token', idToken.toString());
                    this.$router.push('/bookshelf');
                }).catch((err) => {
                // console.log(err);
                console.log('firebase get token error');
            });
        }).catch((err) => {
            this.message = 'メールアドレスかパスワードが間違っています。';
            // alert('ログインエラー');
            // console.log(err);
        });
    }

}
